import { Utils } from "./shared/Utils";
import { runSharedScript } from "./shared/index";
import { ScriptMap } from "./types/ScriptMap";
import { StylingParameterMetadata, } from "./types/StylingParameter";
import { initChatWidget } from "./chat/chatWidget";
document.addEventListener("DOMContentLoaded", () => {
    initChatWidget({
        apiBase: "https://ai-chat-bot-1xm4.onrender.com/api/ai",
        debug: true, // turn off after you see it working
    });
    console.log("[VividChat] direct mount called");
});
// Declare global state object
export const GLOBALVARS = {
    currentPage: null,
    baseURL: "https://vividstorefronts.netlify.app",
};
const defaultOptions = {
    hideHomeLink: false,
    hideCompanyShipTo: false,
    hideAddressBook: false,
    lockAddressBook: false,
    enableDropdown: false,
};
async function loadStorefrontScript(groupID, styling, options) {
    try {
        // Hide the body until everything is loaded. This is to prevent flashing of unstyled content
        $("body").hide();
        // Set global variables and merge options with defaults
        GLOBALVARS.currentPage = Utils.determineCurrentPage();
        const scriptOptions = { ...defaultOptions, ...options };
        const scriptFolder = ScriptMap[groupID];
        if (!scriptFolder) {
            throw new Error(`Module with groupID ${groupID} not found in ModuleMap.`);
        }
        // Conditionally import base styling
        // Don't use shared styling for Canes, TGK
        if (![58, 111].includes(groupID)) {
            await import(/* webpackChunkName: "basestyling" */ `./shared/styles.css`);
        }
        // Apply styling variables if provided
        if (styling)
            setCSSVariables(styling);
        // Run shared script if applicable
        if (groupID !== 58)
            $(() => runSharedScript(scriptOptions));
        // Load the appropriate script module
        const uniqueScript = await import(
        /* webpackChunkName: "uniqueScript" */ `./store_scripts/${scriptFolder}/index.ts`);
        await import(
        /* webpackChunkName: "uniqueStyling" */ `./store_scripts/${scriptFolder}/styles.css`);
        // Execute the main function of the loaded module
        if (uniqueScript && typeof uniqueScript.main === "function") {
            uniqueScript.main();
        }
        else {
            throw new Error("The loaded module does not have a main function.");
        }
    }
    catch (error) {
        console.error(error);
    }
    finally {
        // Show the body once everything is complete
        $("body").show();
    }
}
function setCSSVariables(stylingVariables) {
    const root = $(":root");
    for (const variableName in stylingVariables) {
        const defaultCSSValue = root.css(StylingParameterMetadata[variableName]
            .CSSVariableName);
        if (variableName in StylingParameterMetadata) {
            root.css(StylingParameterMetadata[variableName]
                .CSSVariableName, stylingVariables[variableName] ??
                defaultCSSValue);
        }
    }
}
// Expose loadStorefrontScript to the window
window.loadStorefrontScript = loadStorefrontScript;
